a{text-decoration:none;color:inherit}a:visited{color:#8b0000}a:hover{color:red}@font-face{font-family:Roboto;src:url(/static/Roboto-Regular.ttf),url(/static/Roboto-Bold.ttf),url(/static/Roboto-Light.ttf),url(/static/Roboto-Medium.ttf)}a,body,div,html{font-family:Roboto,serif}.input{padding:0 0 0 30px;border-bottom:1px solid #000;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.input span{display:-webkit-inline-box;height:25px;width:25px;margin-left:-30px;margin-bottom:-8px;background-image:url(/static/search.jpg);background-repeat:no-repeat;background-position:0 100%;background-size:contain}.input span:hover{cursor:pointer;background-size:70%;background-position:50%}.input input{outline:none;border:none;padding-top:5px;padding-bottom:5px;padding-left:10px;font-size:1em;font-family:inherit;font-weight:300;letter-spacing:.05em}.input::-webkit-input-placeholder{color:#d3d3d3;font-family:inherit;font-weight:300}.input:-ms-input-placeholder{color:#d3d3d3;font-family:inherit;font-weight:300}.input::-ms-input-placeholder{color:#d3d3d3;font-family:inherit;font-weight:300}.input::placeholder{color:#d3d3d3;font-family:inherit;font-weight:300}.input:hover{border-bottom:1px solid red}.input[focus-within]{border-bottom:1px solid red}.input:focus-within{border-bottom:1px solid red}.wrapper-products{display:grid;width:100%;grid-template-columns:repeat(auto-fill,minmax(250px,1fr));-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between}.wrapper-products div p{font-size:.95em;text-transform:uppercase;font-weight:700;color:#2f4f4f;margin-left:.3em;margin-bottom:0;margin-top:.2em;padding:.1em}.wrapper-products div span{color:grey;font-weight:500;margin-left:.35em;padding:.1em;font-size:.8em}@media (max-width:520px){.wrapper-product{display:block}.wrapper-product div img{width:70%!important}}